.events {
  padding-inline: 20px;
  margin-bottom: 90px;

  @include onTablet {
    padding-inline: 40px;
    margin-bottom: 100px;
  }

  @include onDesktop {
    padding-inline: 55px;
    margin-bottom: 110px;
  }

  &__title {
    font-family: $second-font-family;
    font-size: 28px;
    font-weight: 700;
    line-height: 125%;
    margin-bottom: 60px;

    @include onTablet {
      font-size: 40px;
      margin-bottom: 76px;
    }

    @include onDesktop {
      font-size: 48px;
      margin-bottom: 70px;
    }
  }

  &__img {
    width: 100%;
    margin-bottom: 20px;
    cursor: pointer;
    object-fit: cover;
    @include hover (transform, scale(1.1));

    @include onTablet {
      margin-bottom: 70px;
    }

    @include onDesktop {
      //width: 370px;
      height: 475px;
      margin-bottom: 60px;
    }
  }

  &__date {
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    color: $color-orange;
    margin-bottom: 10px;
  }

  &__subtitle {
    font-family: $second-font-family;
    font-size: 20px;
    font-weight: 700;
    line-height: 125%;
    margin-bottom: 20px;

    @include onTablet {
      font-size: 24px;
    }
  }

  &__prg {
    color: $color-grey;
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    margin-bottom: 30px;

    @include onTablet {
      margin-bottom: 50px;
    }
  }

  &__btn {
    margin-bottom: 60px;
  }

  &__btn--second {
    margin-bottom: 30px;
  }

  &__btn--arrow {
    margin-left: 8px;
  }

  &--top {
    @include onTablet {
      display: flex;
      justify-content: space-between;
    }
  }

  &--aside {
    @include onTablet {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 30px;
      overflow: hidden;
    }
  }

  &__img--other {
    @include onTablet {
      margin-bottom: 0;
    }
  }
}
